/* AboutPage.css */
.about-page {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

.about-page h1 {
  font-size: 28px;
  margin-bottom: 20px;
  text-align: center;
}

.about-page p {
  margin-bottom: 15px;
  line-height: 1.6;
}




.footer p{
  background-color: transparent;
  color:  #fff;
  padding: 20px;
  text-align: center;
  position: fixed;
  left: 0;
  font-size: 10px ;
  bottom: 0;
  width: 100%;
}

/* Media Query for Responsive Design */
@media (max-width: 768px) {
  .about-page {
    padding: 10px;
  }
}
