/* ContactPage.css */

.contact-page {
  margin: 5%;
  text-align: center;
  
}
body{
  margin: 0;
  padding: 0;

  background-size: cover;
  background-repeat: no-repeat;
}
.contact-info {
  margin-bottom: 20px;
  text-align: center;
}

.contact-info h2 {
  font-size: 20px;
  margin-bottom: 10px;
}

.contact-info p {
  margin-bottom: 5px;
}

.contact-form {
  margin-bottom: 20px;
}

.contact-form h2 {
  font-size: 20px;
  margin-bottom: 10px;
}

.contact-form form {
  display: grid;
  gap: 10px;
}

.contact-form label {
  font-weight: bold;
}

.contact-form input,
.contact-form textarea {
  width: 100%;
  padding: 5px;
}

.contact-form button[type="submit"] {
  padding: 10px 20px;
  background-color: #007bff;
  color: #fff;
  border: none;
  cursor: pointer;
}

.success-message {
  color: green;
  font-size: 18px;
  margin-top: 10px;
}

.contact-form button[type="submit"]:hover {
  background-color: #0056b3;
}
.footer p{
  background-color: transparent;
  color:  #fff;
  padding: 20px;
  text-align: center;
  position: fixed;
  left: 0;
  font-size: 10px ;
  bottom: 0;
  width: 100%;
}

/* ContactPage.css */
.contact-form {
  margin-top: 2rem;
}

.contact-form form {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 1rem;
}

.contact-form label {
  font-weight: bold;
}

.contact-form input,
.contact-form textarea {
  padding: 0.5rem;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.contact-form button[type="submit"] {
  background-color: #007bff;
  color: #fff;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  cursor: pointer;
}

.success-message {
  margin-top: 1rem;
  background-color: #d4edda;
  color: #155724;
  padding: 0.75rem;
  border-radius: 4px;
}
